import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex)

const actions = {
    // ÂÜôÂÖ•Áî®Êà∑ÂêçÂíåÂØÜÁ†ÅÔºåÂêåÊó∂Âú® localStorage ‰πüÂÜôÂÖ•
    whiteUser(context,value){
        // localStorageÂÜôÂÖ•
        localStorage.setItem("user", JSON.stringify(value));
        context.commit("WHITEUSER",value)
    },
    getValueList(context){
        context.state.postTo("/systemServer/getValList",{
            name:context.state.user.name,
            password:context.state.user.password
        },data=>{
            context.commit("CHANGELIST", data)
        })
    },
    getValueLog(context){
        context.state.postTo("/systemServer/getValLog",{
            name:context.state.user.name,
            password:context.state.user.password
        },data=>{
            context.commit("CHANGELOG", data)
        })
    },
    // Âêë list ‰∏≠Ê∑ªÂä†‰∏Ä‰∏™
    addListOne(context,value){
        context.state.postTo("/systemServer/addOne",{
            name:context.state.user.name,
            password:context.state.user.password,
            tag:value.tag,
            url:value.url
        },data=>{
            if (data){
                context.dispatch("getValueList").then(r => {
                    if (r) console.log(r)
                })
            }else alert("Ê∑ªÂä†Â§±Ë¥•")
        })
    },
    // ‰ªé list ‰∏≠Âà†Èô§‰∏Ä‰∏™
    removeListOne(context,i){
        if (confirm(`Á°ÆÂÆöË¶ÅÂà†Èô§Á¥¢Âºï ${i.name} ÂêóÔºü`)) {
            // Áî®Êà∑ÁÇπÂáª‰∫Ü‚ÄúÁ°ÆÂÆö‚ÄùÊåâÈíÆÔºåÁªßÁª≠ÊâßË°åÂêéÈù¢ÁöÑ‰ª£Á†Å
            context.state.postTo("/systemServer/removeOne",{
                name:context.state.user.name,
                password:context.state.user.password,
                tagId:i.id
            },data=>{
                if (data){
                    context.dispatch("getValueList").then(r => {
                        if (r) console.log(r)
                    })
                }else alert("Âà†Èô§Â§±Ë¥•")
            })
        }
    }
}
const mutations = {
    // ÂÜôÂÖ•Áî®Êà∑ÂêçÂíåÂØÜÁ†Å
    WHITEUSER(state,value){
        state.user.name = value.name
        state.user.password = value.password
    },
    CHANGELIST(state,value){
        state.list = value
    },
    CHANGELOG(state,value){
        state.log = value
    }
}
// ÂèØ‰ª•ÁêÜËß£ÊàêÊâÄÊúâÁªÑ‰ª∂ÈÉΩËÉΩËÆøÈóÆÂà∞ÁöÑdata
const state = {
    user:{
        name:'',
        password:""
    },
    list:[],
    log:[],
    // ÂèëÈÄÅ post ÂëΩ‰ª§
    postTo(path,data,callback){
        // ‰∏∫‰∫ÜÁõ¥Êé•ËÆøÈóÆÊüê‰∏™ÈìæÊé•ÁöÑÂÖÑÂºü‰ª¨ÂÜôÁöÑ
        if (!(data.name || data.password)){
            // Ê≤°ÊúâÊï∞ÊçÆÁöÑËØùÂ∞±ÂÖàÂ∞ùËØïÂéªËØªÂèñÊï∞ÊçÆ
            const userVal = JSON.parse(localStorage.getItem("user"))
            // Â¶ÇÊûúÊúâÊï∞ÊçÆ
            if (userVal) {
                this.user = userVal
                data = userVal
            }
        }
        axios.post(path,data).then(
            res => callback(res.data),
            err => alert("ÁΩëÁªúËøûÊé•Â§±Ë¥•"+err)
        )
    },
    // ÁôªÂΩï‰ΩøÁî®ÁöÑÂáΩÊï∞Ôºå‰ºöËøîÂõûÊàêÂäüÂíåÂ§±Ë¥•
    tryLogIn(name,password,callback){
        this.$store.state.postTo("/systemServer/login",{
            name,password
        },callback)
    }

}
// ÂèØ‰ª•ÁêÜËß£ÊàêÊâÄÊúâÁªÑ‰ª∂ÈÉΩËÉΩËÆøÈóÆÂà∞ÁöÑËÆ°ÁÆóÂ±ûÊÄß
const getters = {

}
export default new Vuex.Store({
    actions,
    mutations,
    state,
    getters
})