<template>
  <div id="login-page" class="container">
    <div style="height: 30vh"></div>
    <div class="login-from col-12 offset-0 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-4 offset-lg-4">
      <h2>ÁÆ°ÁêÜÂëòÁôªÈôÜ</h2>
      <div class="form-floating mb-3">
        <input type="text" class="form-control" placeholder="Áî®Êà∑Âêç" v-model="name">
        <label>Áî®Êà∑Âêç</label>
      </div>
      <div class="form-floating">
        <input type="password" class="form-control" placeholder="ÂØÜÁ†Å" v-model="password" @keydown.enter="login">
        <label>ÂØÜÁ†Å</label>
      </div>
      <button class="btn btn-primary" @click="login">ÁôªÈôÜ</button>
    </div>
  </div>
</template>

<script>
import {mapActions,mapState} from 'vuex'
export default {
  name: 'LogIn',
  data(){
    return{
      name:"",
      password:""
    }
  },
  methods:{
    login(){
      // ‰∏çËÉΩ‰∏∫Á©∫
      if (this.name.length !== 0 && this.password.length !== 0){
        const regex = /[<>\\]/;
        // ÊµãËØïÂá†‰∏™ÁâπÊÆäÁ¨¶Âè∑
        if(!regex.test(this.name) && !regex.test(this.password)){
          // Áõ¥Êé•ËøõË°åÁôªÂΩïÊµãËØï
          this.tryLogIn(this.name,this.password,data=>{
            if (data){
              // ‰øùÂ≠òÁî®Êà∑ÂêçÂíåÂØÜÁ†Å
              this.whiteUser({
                name:this.name,
                password:this.password
              })
              // ÊÉÖÁ©∫ËæìÂÖ•Ê°Ü
              this.name = ""
              this.password = ""
              // ÊàêÂäüÂàôÂâçÂæÄ /page
              this.$router.push("/page")
            }else alert("Áî®Êà∑ÂêçÊàñÂØÜÁ†ÅÈîôËØØ")
          })
        }else alert("‰∏çËÉΩÂê´ÊúâÁâπÊÆäÁ¨¶Âè∑")
      }else alert("‰∏çËÉΩ‰∏∫Á©∫")
    },
    ...mapActions(["whiteUser"]),
  },
  computed:{
    ...mapState(["tryLogIn"])
  },
  mounted() {
    // Â∞ùËØïËØªÂèñÊú¨Âú∞Êï∞ÊçÆ
    const userVal = JSON.parse(localStorage.getItem("user"))
    if (userVal){
      this.tryLogIn(userVal.name,userVal.password,data=>{
        if (data){
          // ÊàêÂäüÔºåÂâçÂæÄ /page
          this.whiteUser({
            name: userVal.name,
            password:userVal.password
          })
          this.$router.push("/page")
        }else {
          // Â§±Ë¥•ÔºåÂà†Èô§ ls ÂÜçÂâçÂæÄ /login
          localStorage.removeItem("user");
        }
      })
    }
  }
}
</script>

