<template>
  <div class="container col-12 offset-0 col-md-10 offset-md-1 col-lg-8 offset-lg-2">
    <!-- ÊêúÁ¥¢Ê°Ü -->
    <PageListSearch :valueChange="valueChange"/>

    <!-- ‰ø°ÊÅØÂàóË°® -->
    <transition
        appear
        name="animate__animated animate__bounce"
        enter-active-class="animate__bounceInUp"
        leave-active-class="animate__bounceOut"
    >
      <PageListCards
          :searchValue="searchValue"
          :addClick="addClick"
      />
    </transition>

    <!-- Ê∑ªÂä†ËèúÂçï -->
    <transition
        name="animate__animated animate__bounce"
        enter-active-class="animate__bounceIn"
        leave-active-class="animate__zoomOut"
    >
      <PageListAddForm
          v-show="addOn"
          :allTags="allTags"
      />
    </transition>
  </div>
</template>

<script>
import {mapState,mapActions} from "vuex";
import PageListAddForm from "@/components/page/list/PageListAddForm.vue";
import PageListCards from "@/components/page/list/PageListCards.vue";
import PageListSearch from "@/components/page/list/PageListSearch.vue";
export default {
  name: 'PageList',
  data(){
    return {
      addOn:false,
      searchValue:"",
    }
  },
  components:{
    PageListSearch,
    PageListAddForm,
    PageListCards
  },
  computed:{
    allTags(){
      const data = []
      for (let i of this.list){
        data.push(i.name)
      }
      return data
    },
    ...mapState(["list"])
  },
  methods:{
    ...mapActions(["getValueList"]),
    addClick(){
      this.addOn = true
    },
    valueChange(val){
      this.searchValue = val
    },
  },
  mounted(){
    const userVal = JSON.parse(localStorage.getItem("user"))
    if (userVal){
      this.getValueList()
    }else {
      this.$router.push("/login")
    }
  },
  beforeRouteEnter(to,from,next){
    try {
      const nav = document.getElementById('navbarSupportedContent')
      nav.classList.toggle('show');
    }catch (e) {
      console.log("Áõ¥Êé•ËÆøÈóÆÂá∫Áé∞Ê≠§logÂèØ‰ª•ÂøΩËßÜ")
      console.log(e)
    }
    next()
  }
}
</script>

